*{
    margin: 0;
    padding: 0;
}

.written-question-container {
    width: 1400px;
    height: 100vh;
    margin: 5vh auto 15vh;
    text-align: center;
}

.mypage-written-question-nav {
    width: 80vh;
    margin: 9vh auto;
}

.mypage-written-question-nav > li {
    display: inline-flex;
    justify-content: center;
    width: 20%;
    height: 5vh;
    padding: 0.5vh 1vh;
    font-size: large;
    font-weight: bold;
    border: 1px solid rgb(2, 136, 180);
    border-radius: 9px 6px 0 0;
    border-bottom-style: none;
    list-style: none;
    text-align: center;
    align-items: center;
}

.mypage-written-question-nav > li:nth-child(3) {
    background-color: rgb(2, 136, 180);
    color: white;
}

.question-title {
    margin: 1.2vh 1vh;
    position: absolute;
}

.question-card,
.no-question-message {
    width: 55vh;
    padding: 1vh;
    margin: 3vh auto;
    border: 1px solid rgb(2, 136, 180);
    border-radius: 6px;
    font-size: 16px;
}

.mypage-question-edit {
    padding: 1vh 3vh;
    background-color: rgba(2, 136, 180, 1);
}

.mypage-question-edit {
    padding: 1vh 3vh;
    background-color: rgba(2, 136, 180, 0.7);
}

.mypage-question-delete {
    padding: 1vh 3vh;
    background-color: rgba(220, 90, 90, 1);
}

.mypage-question-delete {
    padding: 1vh 3vh;
    background-color: rgba(220, 90, 90, 0.7);
}